

.About{
    border: 1px solid rebeccapurple;
    height: 500px;
    display: flex;
    background-color: rgb(236,240,243);
}
.name{
    /* border: 1px solid black; */
    width: 70%;
}
.mypic{
    /* border: 1px solid blue; */
    width: 25%;
    border-radius: 50%;
    margin-top: 3%;
    margin-right: 5%;
    margin-left: 5%;
}
.mypic>img{
    width: 100%;
    height: auto;
    border-radius: 50%;
}
.headname{
    /* border: 1px solid blue; */
    width: fit-content;
    margin-left: 5%;
    margin-top: 10%;
}
.info{
    /* border: 1px solid blueviolet; */
    box-shadow: rgba(0, 0, 0, 0.05) 0px 0px 0px 1px;
    border-radius: 2px;
    width: 60%;
    font-weight: 600;
    line-height: 1.5rem;
    margin-left: 5%;
    padding: 5px;
    margin-top: 2%;
 
}
.info>p{
    text-align: justify; 
    /* color: slategray; */
    font-size: large;
}
.icon{
    /* border: 1px solid teal; */
    width: 40%;
    margin-left: 10%;
    margin-top: 5%;
    display: flex;
    justify-content: space-evenly;
}
.icon>div{
    border: 1px solid black;
    border-radius: 5px;
    width: fit-content;
    padding: 3px;
   
}
.icon>div>a>img{
    width: 90%;
    height:30px;
    
}

@media only screen  and (max-width: 820px) {
    .About{
        border: 1px solid rebeccapurple;
        display: flex;
        height: auto;
        background-color: rgb(236,240,243);
    }
    .name{
        border: 1px solid black;
        width: 100%;
    }

    .mypic{
        /* border: 1px solid blue; */
        width: 35%;
        margin-top: 3%;
        margin-left: -70%;
    }
    .mypic>img{
        width: 90%;
        height: 40%;
        border-radius: 50%;
    }
    .headname{
        border: 1px solid blue;
        width: 80%;
        margin-top: 40%;
        margin-left: 10%;
    }
    .info{
        border: 1px solid blueviolet;
        box-shadow: rgba(0, 0, 0, 0.05) 0px 0px 0px 1px;
        border-radius: 2px;
        width: 80%;
        font-weight: 600;
        line-height: 1.5rem;
        margin-left: 10%;
        padding: 5px;
        margin-top: 2%;
     
    }
    .info>p{
        text-align: justify; 
        color: slategray;
        font-size: large;
    }
    .icon{
        border: 1px solid teal;
        width: 40%;
        margin-left: 30%;
        margin-top: 2%;
        margin-bottom: 5%;
        display: flex;
        justify-content: space-evenly;
    }
    .icon>div{
        border: 1px solid black;
        border-radius: 5px;
        width: fit-content;
        padding: 3px;
       
    }
    .icon>div>a>img{
        width: 90%;
        height:30px;
        
    }
}