

.Nav{
    border: 1px solid red;
    display: flex;
    justify-content: space-between;
    background-color: rgb(12, 128, 217);
}
.Nav>div>img{
    width:50px;
    margin-left:80%;
    border-radius: 50%;

}
.links{
    /* border: 1px solid black; */
    width: 30%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin-right: 5%;

}

.secondNav{
    border: 1px solid red;
    background-color: rgb(236,240,243);
    height: 50px;
    display: none;
}
.ThirdNav{
    border: 1px solid red;
    height: 50px;
    display: none;
    background-color: rgb(236,240,243);
}


@media only screen and (min-width:421px) and (max-width: 820px) {
    .secondNav{
        display: block;
        display: flex;
    }
    .Nav{
        display: none;
    }
    .menu{
        /* border: 1px solid blue; */
        color: teal;
        border: none;
        width: fit-content;
          display: flex;
          margin-left: 90%;
          margin-right: 5%;
          justify-content: center;
          align-items: center;
    }
  }

  @media only screen and (max-width: 420px) {
    .secondNav{
        display: none;
    }
    .Nav{
        display: none;
    }
    .ThirdNav{
        display: block;
    }
    .menu{
        /* border: 1px solid blue; */
        color: black;
        border: none;
        width: fit-content;
          display: flex;
          margin-left: 80%;
          margin-right: 5%;
          justify-content: center;
          align-items: center;
    }
  }
  
  